






























import {
	mapState
} from 'vuex';

export default {
	computed: {
		...mapState(['userInformation'])
	},
	data() {
		return {
			isH5Plus: true,
			userinfo: {},
			orderTypeLise:[
				//name-标题 icon-图标 badge-角标
				{name:'待付款',icon:'l1.png',badge:1},
				{name:'待发货',icon:'l2.png',badge:2},
				{name:'待收货',icon:'l3.png',badge:6},
				{name:'待评价',icon:'l4.png',badge:9},
				{name:'退换货',icon:'l5.png',badge:0}
			],
			severList:[
				[
					{name:'我的收藏',icon:'point.png'},
					{name:'优惠券',icon:'quan.png'},
					{name:'红包',icon:'momey.png'},
					{name:'任务',icon:'renw.png'},
				],
				[
					{name:'积分明细',icon:'mingxi.png'},
					{name:'抽奖',icon:'choujiang.png'},
					{name:'收货地址',icon:'addr.png'},
					{name:'银行卡',icon:'bank.png'},
					{name:'安全中心',icon:'security.png'},
					{name:'在线客服',icon:'kefu.png'}
				]
			],
		};
	},
	onShow() {
		//更新vuex存储
		this.$store.commit('userInfoUpdate');
		//加载
		this.init();
	},
	methods: {
		init() {
			//显示用户信息
			console.info(JSON.stringify(this.userinfo))
			//用户信息	
			if(typeof(this.userInformation.avatarUrl) != 'undefined'){
				this.userinfo = this.userInformation
			}else{
				this.userinfo = {
					avatarUrl: '../../../static/userInfo/face.png',
					nickName: '',
					autograph: '点击左侧头像登录',
				}
			}
			
		},
		//用户点击订单类型
		toOrderType(index){
			uni.showToast({title: this.orderTypeLise[index].name});
		},
		//用户点击列表项
		toPage(list_i,li_i){
			uni.showToast({title: this.severList[list_i][li_i].name});
		},
		//点击头像进行登录
		logins(e) {
			//未登陆状态下点击才会登录
			if(typeof(this.userInformation.avatarUrl) === 'undefined'){
				let href = '/pages/loginAuthorization/login/login';
				if(href){
					uni.navigateTo({
						url: href
					});
				}else{
					uni.showToast({ title: '功能开发中...', icon: "none" });
				}
			}
		},
		//点击跳转到个人设置页面
		setting() {
			uni.navigateTo({
				url: '/pages/personalCenter/personalSettings/personalSettings'
			});
		}
	}
}
